import helper from '../../common/helper.js';
export default{
	data(){
		return{
			userInfo:null
		}
	},
	onLoad:function(){
		let that = this;
		uni.authorize({
			scope:'scope.userInfo',
			success:function(res){
				uni.getStorage({
					key:'userInfo',
					success:function(res){
						console.log(res)
						if(res !=undefined){
							that.userInfo = res.data;
						}
						
					}
				})
			}
		})
		
	},
	methods:{
		bindInfo:function(res){
			let that = this;
			
			console.log(res.detail.rawData);
			//用户同意授权;
			if(res.detail.errMsg == 'getUserInfo:ok'){
				//获取到的用户信息为JSON数据格式,需要将其转换为Object,否则模板引擎无法渲染;
				that.userInfo = JSON.parse(res.detail.rawData);
				//将用户信息存入缓存,下次获取无需调用接口;
				uni.setStorage({
					key:'userInfo',
					data:this.userInfo
				})
			}
		},
		handleGetUserInfo:function(res){
			let that = this;
			
			console.log(res.detail.rawData);
			//用户同意授权;
			if(res.detail.errMsg == 'getUserInfo:ok'){
				//获取到的用户信息为JSON数据格式,需要将其转换为Object,否则模板引擎无法渲染;
				that.userInfo = JSON.parse(res.detail.rawData);
				//将用户信息存入缓存,下次获取无需调用接口;
				uni.setStorage({
					key:'userInfo',
					data:this.userInfo
				})
			}
			
			
			
			uni.login({
				success:function(res){
					uni.request({
						url:helper.webroot+'api/login/openid.html?spm=m-61-73',
						data:{
							code:res.code
						},
						method:"POST",
						success:function(res){
							console.log("res:");
							console.log(res);
							let id = res.data.openid;
							uni.setStorage({
								key:'id',
								data:res.data.openid
							})
							uni.setStorage({
								key:'session',
								data:res.data.sessionID
							})
							
							//注册;
							uni.request({
								url:helper.webroot+"api/login/userinfo",
								method:"POST",
								data:{
									nickname:that.userInfo.nickName,
									sex:that.userInfo.gender,
									country:that.userInfo.country,
									province:that.userInfo.province,
									city:that.userInfo.city,
									headimg:that.userInfo.avatarUrl,
									openid:id
								},
								success:function(res){
									console.log("注册成功");
									console.log(res)
								}
							})
						}
					})
				}
			})
			
		}
	}
}
